
import { PropType, defineComponent } from "vue";
import { CustomerInfoInquiryDetail } from "@/shared/type/CIB20100114.type";
import { COMMON_KEY_VALUE } from "@/shared/constants/common.const";

export default defineComponent({
  name: "CustomerInformation",

  props: {
    data: {
      type: Object as PropType<CustomerInfoInquiryDetail>,
      required: true
    }
  },

  data() {
    return {
      COMMON_KEY_VALUE,
      branch: ''
    }
  },

  computed: {
    customerCorpIndTypeCode(): string {
      return this.$codeConverter.convertValue('subscriptionCustomerType', this.data.customerType)
    },
    dateOfBirth(): string {
      let date = ''
      if(this.data.dateOfBirth){
        date = `${this.data.dateOfBirth.slice(0,4)}${this.data.dateOfBirth.slice(4,6)}${this.data.dateOfBirth.slice(6,8)}`
      }
      return this.$datetime.formatStringToStringDate(date)
    },
  },
})
