
import { defineComponent } from "vue";

import Pagination from '@/shared/components/Pagination/Pagination.vue';
import CustomerInformation from "@/shared/components/CustomerInformation/CustomerInformation.vue";
import { IBCustomerInquiry, CIB20200111_REQ, CIB20200111_RES } from '@/shared/type/CIB20200112.type'
import { CIB20100114_RES, CustomerInfoInquiryDetail } from "@/shared/type/CIB20100114.type";
import { PAGE_SIZE } from "@/shared/constants/common.const";
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';

export default defineComponent({
    name: "cib202001sc00",
    components: {
        Pagination,
        CustomerInformation,
        BreadCrumb
    },
    data() {
        const customerNo = this.$route.query.customerNo;
        const userID = this.$route.query.userID;
        const userList = [] as IBCustomerInquiry[];
        const customerInfo = {} as CustomerInfoInquiryDetail;
        const header = [
            { columnName: this.$t('cib202001sc00.column.no') },
            { columnName: this.$t('cib202001sc00.column.change_date') },
            { columnName: this.$t('cib202001sc00.column.change_time') },
            { columnName: this.$t('cib202001sc00.column.service') },
            { columnName: this.$t('cib202001sc00.column.change_item') },
            { columnName: this.$t('cib202001sc00.column.branch') },
            { columnName: this.$t('cib202001sc00.column.branch_name') },
            { columnName: this.$t('cib202001sc00.column.before_change_content') },
            { columnName: this.$t('cib202001sc00.column.after_change_content') },
            { columnName: this.$t('cib202001sc00.column.transaction_teller_id') }
        ];

        return {
            userID,
            customerNo,
            userList,
            customerInfo,
            header,
            totalItems: 0,
            currentPage: 1,
            pageSize: PAGE_SIZE,
            startingIndex: 1,
            isReset: false
        };
    },
    mounted() {
        this.getCustomerInformation();
        this.getCustomerHistoryList();
    },
    methods: {
        async getCustomerInformation() {
            const reqBody = {
                customerNo: `${this.customerNo}`
            }

            const responseData = await this.$request.getRequest('CIB20100114', reqBody, false) as CIB20100114_RES;

            if (responseData.header.result) {
                this.customerInfo = responseData.body;
            }
        },

        async getCustomerHistoryList() {
            const reqBody: CIB20200111_REQ = {
                pageSize: `${this.pageSize}`,
                userID: `${this.userID}`,
                pageNumber: `${this.currentPage}`
            };

            const responseData = await this.$request.getRequest("CIB20200111", reqBody, false) as CIB20200111_RES;

            if (responseData?.header.result) {
                this.userList = responseData?.body.list.map((item: IBCustomerInquiry) => {
                    return {
                        changeTypeCode: this.$codeConverter.convertValue("ChangeTypeCode", item.changeTypeCode),
                        serviceCeaseReasonCode: item.serviceCeaseReasonCode,
                        changeTime: this.$datetime.formatStringToTime(item.changeTime),
                        historyContent: item.historyContent,
                        serviceID: item.serviceID,
                        transactionTellerID: item.transactionTellerID,
                        authorizationNo: item.authorizationNo,
                        userID: item.userID,
                        changeDate: this.$datetime.formatStringToStringDate(item.changeDate),
                        afterChangeItemContent: item.afterChangeItemContent,
                        seqNo: item.seqNo,
                        beforeChangeItemContent: item.beforeChangeItemContent,
                        handleBranchCode: item.handleBranchCode,
                        handleBranchName: item.handleBranchName
                    }
                });
                this.totalItems = responseData.body.totalCount;
            }
        },

        handleList() {
            this.$router.push("CIB201001SC00");
        },

        handlePagination(pageNumber: number) {
            this.currentPage = pageNumber;
            this.getCustomerHistoryList();
        },

        handleStartingIndex(startingIndex: number) {
            this.startingIndex = startingIndex;
        }
    },

});
